*, :before, :after {
  box-sizing: border-box;
}

body {
  color: var(--color-text);
  background-color: var(--color-bg);
  margin: 0;
  padding: 3rem;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
}

img {
  max-width: 100%;
  display: block;
}

:root {
  --color-bg: #121226;
  --color-header-bg: #202046;
  --color-text: #ededed;
}

.wrapper {
  width: min(100% - 40px, 68.75rem);
  margin-inline: auto;
}

.wrapper.has-padding {
  padding-block: 40px;
}

@media (width >= 43.75rem) {
  .wrapper.has-padding {
    padding-block: 60px;
  }
}

.rounded {
  border-radius: 16px;
  overflow: hidden;
}

.header {
  width: 100vw;
  height: 100vh;
}

.nav {
  background-color: var(--color-header-bg);
  flex-direction: row;
  justify-content: space-between;
  width: 100vw;
  height: 5rem;
  padding: 0 2rem;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.nav__link {
  font-size: 1rem;
}

.nav__link--icon {
  fill: #f0f8ff;
  width: 2.5rem;
  height: 2.5rem;
}

.nav__bar {
  flex-direction: row;
  gap: 1rem;
  list-style: none;
  display: flex;
}

.nav__bar--item {
  background-color: #00f;
  border-radius: 1.5rem;
  padding: .7rem 1.4rem;
}

.nav__items {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.nav__item {
  color: var(--color-text);
  font-weight: 500;
  list-style: none;
}

.nav__item--logo {
  margin-left: -3rem;
}
